.info {
    padding-top: 0;
    padding-bottom: 50px;
    margin: 0;
    height: 100vh;
    width: 40%;
    overflow: hidden;
    white-space: nowrap;
}

.main_pic {
    height: 65%;
    margin-top: 25px;
    margin-bottom: 50px;
    margin-right: 50px;
    margin-left: 50px;
    border-radius: 30px;
    text-align: center;
    background-image: url('../../img/main_photo.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: height 0.5s ease;
    opacity: 0;
    transition: opacity 1s ease, height 0.5s ease;
}

